$color-yellow: #DED95C;
$color-violet: #9D4FCC;
$color-red: #EA7979;

* {
    box-sizing: border-box; }

html {
    font-family: Montserrat; }

.container {
    max-width: 1140px;
    margin: 0 auto;
    position: relative; }

.main-section {
    position: relative;
    min-height: 778px;
    background: #f0f0f0;
    overflow: hidden;

    &__content-container {
        display: flex;
        flex-direction: row;
        position: relative; }

    &__nav-container {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        height: 69px;
        align-items: center;
        padding-top: 16px; }

    &__logo {
        width: 96px;
        height: 49px; }

    &__first-elipse {
        position: absolute;
        top: 169px;
        right: 14px; }

    &__second-elipse {
        position: absolute;
        right: -120px;
        top: 650px; } }

.nav-menu {
    &__list {
        display: flex;
        flex-direction: row; }
    &__list-item {
        margin-right: 56px;
        font-size: 18px;
        &:last-child {
            margin-right: 0px; }

        a {
            text-decoration: none;
            color: #000; } } }



.left-wrapper {
    position: relative;
    img {
        position: relative;
        z-index: 5; }

    &__rectangle-yellow {
        position: absolute;
        top: 160px;
        left: -80px;
        width: 599.92px;
        height: 174.14px;
        background: $color-yellow;
        border-radius: 87px;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        transform: rotate(-60.16deg);
        z-index: 4; }

    &__rectangle-violet {
        position: absolute;
        top: 190px;
        left: -35px;
        width: 599.92px;
        height: 174.14px;
        background: $color-violet;
        border-radius: 87px;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        transform: rotate(-46.08deg);
        z-index: 3; }

    &__rectangle-red {
        position: absolute;
        top: 240px;
        left: 45px;
        width: 599.92px;
        height: 174.14px;
        background: $color-red;
        border-radius: 87px;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        transform: rotate(-22.85deg);
        z-index: 2; } }

.right-wrapper {
    width: 350px;
    position: absolute;
    top: 100px;
    right: 10px;

    &__icons-box {
        margin-bottom: 1px;
        padding-left: 10px; }

    &__text {
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 400;
        font-size: 18px;
        margin-bottom: 20px; }

    &__title {
        margin-bottom: 5px; } }

.icons {
    display: inline-flex;
    margin-bottom: 5px;

    &__item {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 60px;
        height: 60px;
        border: 1px solid #000;
        border-radius: 100px;
        margin-right: 35px;

        &:last-child {
            margin-right: 0px; }

        i {
            font-size: 40px; } } }

.title {
    font-size: 72px;
    position: relative;
    margin: 0;

    &__hr {
        width: 55px;
        margin: 0;
        height: 1px;
        background: #000;
        margin: 0px;
        margin-bottom: 25px; } }

.buttons {
    display: inline-flex;

    &__main-btn {
        background-color: #DED95C;
        border: 1px solid #000;
        border-radius: 87px;
        margin-right: 52px;
        width: 191px;
        height: 71.42px;
        filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
        text-align: center;
        font-size: 16px; }


    &__icon-btn {
        display: inline-flex;
        align-items: center;

        img {
            margin-right: 12px; } } }

.elipse {
    width: 160px;
    height: 160px;
    background: linear-gradient(139.27deg, #DED95C 15.5%, rgba(157, 79, 204, 0.48) 54.65%, #EA7979 84.57%), #D9D9D9;
    opacity: 0.3;
    border-radius: 150px;
    display: flex;
    justify-content: center;
    align-items: center;

    &_dig {
        width: 282px;
        height: 282px; }

    &__subtract {
        width: 113px;
        height: 113px;
        background: #f0f0f0;
        border-radius: 150px;

        &_big {
            width: 188px;
            height: 188px; } } }

.second-section {
    min-height: 700px;
    overflow: hidden;

    &__container {
        padding-top: 32px;
        position: relative; }

    &__title {
        margin-bottom: 40px; }

    &__cards-box {
        display: inline-flex;
        justify-content: center;
        margin-bottom: 35px; }

    &__cards-item {
        margin-left: 17.1px;

        &:last-child {
            margin-right: 0; } }

    &__icons-box {
        display: flex;
        justify-content: space-evenly; }

    &__icons-item {


        &:last-child {
            margin-right: 0; } }

    &__first-elipse {
        position: absolute;
        top: 80px;
        right: -60px;
        z-index: -1; }

    &__second-elipse {
        position: absolute;
        top: 400px;
        left: -50px;
        z-index: -1; } }

.cards-item {
    position: relative;
    width: 360px;
    height: 360px;
    background: #D9D9D9;
    border-radius: 10px;
    display: flex;
    align-items: flex-end;

    &__img {
        height: 344px; }

    &__text {
        width: 280px;
        font-size: 36px;
        text-align: center;
        transform: rotate(90deg);
        position: absolute;
        top: 135px;
        right: -50px; } }

.cards-item_orange {
    background: $color-violet; }

.cards-item_blue {
    background: $color-red; }

.icons-item {
    display: inline-flex;
    align-items: center;

    &__img {
        margin-right: 20px;
        width: 80px; }

    &__text {
        font-size: 18px;

        span {
            font-size: 48px; } } }

.third-section {
    min-height: 640px;
    background: #F0F0F0;
    z-index: 0;
    position: relative;
    overflow: hidden;

    &__container {
        display: flex;
        flex-direction: row;
        align-items: center;
        padding-left: 167px;
        justify-content: space-between;
        padding-top: 46px; }

    &__right-wrapper {
        max-width: 480px; }

    &__right-title {
        margin-bottom: 20px; }

    &__text {
        font-size: 18px;
        margin-bottom: 30px; }

    &__first-elipse {
        position: absolute;
        top: 50px;
        left: 70px;
        z-index: -1; }

    &__figure {
        position: absolute;
        top: 210px;
        left: 10px;
        z-index: -1; }

    &__second-elipse {
        position: absolute;
        right: -120px;
        top: -80px; } }

.title__hr_top {
    bottom: 75px; }

.footer {
    position: relative;
    height: 272px;
    overflow: hidden;
    &__container {
        min-height: 272px;
        display: flex;
        align-items: center;
        justify-content: flex-end; }

    &__title {
        margin-bottom: 20px; }

    img {
        position: absolute;
        top: 20px;
        left: -100px;
        bottom: 0px; } }


